import Vue from 'vue'
import App from './App.vue'
import router from './router'
// echarts图表
import echarts from 'echarts'

Vue.prototype.$echarts = echarts;

// 适配rem
import 'lib-flexible/flexible';

// axios请求方式
import VueAxios from 'vue-axios'
import axios from 'axios'

// 引用element组件
Vue.use(ElementUI);
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import { Carousel,Upload,Rate, } from 'element-ui';

// 引用vant组件
import { Button,Field,Form,Divider,Tabbar, TabbarItem,Search,Toast,Tab, Tabs,NavBar,NoticeBar,Empty,Icon,Popup,Cell, CellGroup,Uploader,RadioGroup, Radio,Dialog,Notify,Swipe,SwipeItem,Overlay,Calendar,SwipeCell } from 'vant';
Vue.use(Button)
   .use(Field)
   .use(Form)
   .use(Divider)
   .use(Tabbar)
   .use(TabbarItem)
   .use(Search)
   .use(Toast)
   .use(Carousel)
   .use(Tab)
   .use(Tabs)
   .use(NavBar)
   .use(Upload)
   .use(NoticeBar)
   .use(Empty)
   .use(Rate)
   .use(Icon)
   .use(Popup)
   .use(Cell)
   .use(CellGroup)
   .use(Uploader)
   .use(RadioGroup)
   .use(Radio)
   .use(Dialog)
   .use(Notify)
   .use(Swipe)
   .use(SwipeItem)
   .use(Overlay)
   .use(Calendar)
   .use(SwipeCell)
;

// 注册axios请求方式
Vue.use(VueAxios, axios)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
